import React, { Component } from 'react';
import { Container, Card, Form, OverlayTrigger, Tooltip, Image } from 'react-bootstrap';
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import ManageRules from './ManageRules';
import { planUpgrade } from './utils';
import HttpClient from '../../../../actions/http-client';
import { MediaBaseUrl, MODIFY_RULE, RULE_DELETE, RULE_LIST, RULE_SORT } from '../../../../actions/api';
import moment from 'moment';
import RuleListSkel from './RuleListSkel';
import { shopifyApp, shopifyForm, SWAL_FIRE, TOAST_LOADING, TOAST_UPDATE } from '../../../../utils';
import { connect } from 'react-redux';
import SubHeaderCrumbs from '../CommonComponents/SubHeader';
import SideMenu from '../Moderation/SideMenu';
import './autopilot.scss';


const reorder = (list, startIndex, endIndex) => {
   const result = Array.from(list);
   const [removed] = result.splice(startIndex, 1);
   result.splice(endIndex, 0, removed);
   return result;
};

const getItemStyle = (isDragging, draggableStyle) => ({
   userSelect: "none",
   borderBottom: `1px solid #F5F5F5`,
   ...draggableStyle
});

class AutoPilot extends Component {
   constructor(props) {
      super(props);
      this.state = {
         defRule: false,
         defIndex: 0,
         ruleList: [],
         loading: false,
         ruleData: {},
         reset: false,
         wallId: localStorage.getItem('wallId'),
         spinner: false,
         editId: null,
         editStatus: null,
         editOrder: null,
         isDelete: false,
         isStatus: false,
         activeDefIndex: null
      };
      this.onDragEnd = this.onDragEnd.bind(this);
   }

   timeouts = []

   componentDidMount() {
      const { ruleList } = this.state
      if (ruleList?.length <= 0) this.getRules()
   }

   componentDidUpdate(prevProps) {
      if ((this.props.manageFeeds?.loading != prevProps.manageFeeds?.loading) && this.props.manageFeeds?.loading != true) {
         this.setState({ reset: true, wallId: localStorage.getItem('wallId') }, () => {
            const timer1 = setTimeout(() => {
               this.setState({ reset: false })
            }, 100);
            this.timeouts.push(timer1)
            this.getRules()
         }
         )
      }
   }

   getRules = (modify) => {
      if (!modify) this.setState({ loading: true })
      new HttpClient().get(`${RULE_LIST}/${this.state.wallId}`).then(res => {
         const { responseData } = res.data
         this.setState({ ruleList: responseData, loading: false })
      })
   }

   modifyRule = (payload, status, callback) => {
      const { editId, wallId } = this.state
      const toastId = TOAST_LOADING();
      const isShopify = shopifyApp();
      if (!status) this.setState({ spinner: true })
      if (payload.actionsType == 3) this.setState({ isStatus: true })
      new HttpClient().post(`${MODIFY_RULE}`, { ...payload, wallId: wallId }).then(response => {
         const { responseMessage } = response.data
         if (isShopify) {
            window.saveBar.set({ saveAction: { loading: false } });
            shopifyForm('hide');
         }
         TOAST_UPDATE(toastId, "success", responseMessage)
         if (payload.actionsType == 1) this.setState({ reset: true, spinner: false, isStatus: false, activeDefIndex: null }, () => {
            const timer2 = setTimeout(() => {
               this.setState({ reset: false })
            }, 100);
            this.timeouts.push(timer2)
         })
         else if (payload.actionsType == 3 && payload.id == editId) {
            this.setState({ editStatus: payload.status, spinner: false, isStatus: false, activeDefIndex: null }, () => {
               const timer3 = setTimeout(() => {
                  this.setState({ editStatus: null })
               }, 10);
               this.timeouts.push(timer3)
            })
         }
         else {
            this.setState({ spinner: false, isStatus: false, activeDefIndex: null }, () => callback ? callback() : null)
            const timer2 = setTimeout(() => {
               this.setState({ reset: true })
            }, 100);
            this.timeouts.push(timer2)
         }
         this.getRules(true)
      }).catch(err => {
         if (isShopify) {
            window.saveBar.set({ saveAction: { loading: false } });
            shopifyForm('hide');
         }
         this.setState({ spinner: false })
         TOAST_UPDATE(toastId, "error", 'Some error occured, please try again later.')
      })
   }


   confirmDelete = (id) => {
      const common = window?.APP_CONTENT?.common ?? {};
      SWAL_FIRE({
         title: common?.are_you_sure ?? "",
         text: common?.delete_item ?? "",
         icon: 'warning',
         showCancelButton: true,
         cancelButtonText: common?.cancel ?? "",
         confirmButtonText: common?.delete ?? ""
      }).then((result) => {
         if (result.value) {
            this.deleteRule(id)
         }
         else if (result.dismiss === "cancel") { }
      });
   }

   deleteRule = (id) => {
      let { ruleList, editId, wallId } = this.state
      const toastId = TOAST_LOADING()
      this.setState({ isDelete: true })
      new HttpClient().delete(`${RULE_DELETE}/${id}/${wallId}`).then(response => {
         const { responseMessage } = response.data
         TOAST_UPDATE(toastId, "success", responseMessage)
         if (id == editId) this.setState({ reset: true }, () => {
            const timer4 = setTimeout(() => {
               this.setState({ reset: false })
            }, 100);
            this.timeouts.push(timer4)
         })
         this.setState({ ruleList: ruleList.filter(rule => rule.id != id), isDelete: false })
      }).catch(err => {
         TOAST_UPDATE(toastId, "error", 'Some error occured, please try again later.')
      })
   }

   changeStatus = (id, status) => {
      const payload = { id: id, status: Number(status), actionsType: 3 }
      this.modifyRule(payload, true)
   }

   onDragEnd(result) {
      if (!result.destination) return;

      const items = reorder(this.state.ruleList, result.source.index, result.destination.index);
      const toastId = TOAST_LOADING()
      let rules = []
      items.map((item, index) => (rules.push({ [item.id]: index })));
      new HttpClient().post(RULE_SORT, { rules: rules, wallId: this.state.wallId }).then(response => {
         let order = rules.find(rule => rule.hasOwnProperty(this.state.editId))
         if (order) this.setState({ editOrder: order[this.state.editId] }, () => {
            const timer5 = setTimeout(() => {
               this.setState({ editOrder: null })
            }, 10);
            this.timeouts.push(timer5)
         })
         this.getRules(true)
         const { responseMessage } = response.data
         TOAST_UPDATE(toastId, "success", responseMessage)
      }).catch(err => {
         this.getRules(true)
         TOAST_UPDATE(toastId, "error", 'Some error occured, please try again later.')
      })
      this.setState({ ruleList: items });
   }

   setDefault = (index) => {
      this.setState({ defRule: true, defIndex: index, activeDefIndex: index }, () => {
         const timer6 = setTimeout(() => {
            this.setState({ defRule: false, defIndex: 0 })
         }, 200);
         this.timeouts.push(timer6)
      })
   }

   componentWillUnmount() {
      this.timeouts.forEach((timeout) => clearTimeout(timeout));
      this.timeouts = []; // Reset the array
   }

   render() {
      const { defRule, defIndex, ruleList, loading, ruleData, reset, spinner, editId, editStatus, editOrder, isDelete, isStatus, activeDefIndex } = this.state
      const { planId, navigate, ownerId, allWalls, userData } = this.props
      const getWallData = allWalls?.find((e) => e.id == localStorage.getItem('wallId')) ?? {};
      const truSense = userData?.truSense;
      const isShopify = shopifyApp();
      const pageContent = window?.APP_CONTENT?.auto_pilot ?? {};
      const breadCrumbs = window?.APP_CONTENT?.['sideBar'];
      return <>
         {allWalls?.length ? <SubHeaderCrumbs key={getWallData?.id} showGallerySwitcher={false}
            crumbs={[{ name: getWallData?.name ?? "", icon: "grid-2-plus", imgUrl: getWallData?.thumbnail ?? "" }, { name: breadCrumbs?.home, link: "/home" }, { name: breadCrumbs?.contentGallery, link: "/content" }, { name: getWallData?.name, link: `/content/moderation/${getWallData?.id}` }, { name: breadCrumbs?.autopilot, link: `/autopilot` }]} /> : null}
         <div className={`custom-side-menu d-flex ${isShopify ? "isShopify" : ""}`}>
            <SideMenu {...{ getWallData }} />
            <div className="custom-main-content">
               <div className='d-flex flex-column flex-column-fluid w-100 full-content'>
                  <Container fluid className={isShopify ? "" : "container-autopilot"}>
                     {/*  */}
                     <div className="d-flex justify-content-between position-relative w-100">
                        <div className="mb-sm-3 mb-lg-5 mb-xl-5 mb-md-4 mb-3">
                           <h3 className='text-capitalize fs-5 cont-title mb-0'>{pageContent?.title ?? ""}</h3>
                           {/* <p className="text-muted">{pageContent?.msg ?? ""}</p> */}
                        </div>
                        <div onClick={() => navigate(`/content/moderation/${localStorage.getItem('wallId')}`)} style={{ right: '0', top: '10px', zIndex: '8', height: '20px', width: '20px', display: 'flex', alignItems: 'center', justifyContent: 'center' }} className="position-absolute cursor-pointer rounded-2 text-muted">
                           <FontAwesomeIcon icon="xmark" iconClass="cursor-pointer fs-3" />
                        </div>
                     </div>
                     {/*  */}
                     <Card className="a_header_">
                        {/* <Card.Header>
                        <div className='f-between flex-wrap w-100'>
                           <div className='d-flex align-items-center flex-wrap my-2'>
                              <h1 className='text-dark font-weight-bold me-4 fs-6 mb-0'>
                                 Auto pilot
                              </h1>
                           </div>
                        </div>
                        </Card.Header> */}
                        <Card.Body className="d-flex flex-wrap flex-lg-nowrap align-items-start p-0 aside_body flex-lg-row-reverse align-items-stretch">
                           <div className="d-lg-flex flex-column flex-shrink-0 overflow-auto rule_guide w-100">
                              {loading ? <RuleListSkel /> : ruleList?.length == 0 ? <React.Fragment key={0}><div className="w-100">
                                 <h4 className="border-bottom p-4 fs-6 mb-0">{pageContent?.here_all ?? ""}:</h4>
                              </div>
                                 <div className='px-5 mt-3'>
                                    {pageContent?.rule_steps?.length ? pageContent?.rule_steps?.map((Item, index) =>
                                       <div key={index} className=" rule-box d-flex flex-column position-relative pb-2">
                                          <h5 className="d-flex fs-8 mb-0">
                                             <span className='bg-body d-inline-block' style={{ zIndex: '1' }}>
                                                <div className="f-center me-2 h-26px w-26px rounded-1 bg-dark me-2" style={{}}>
                                                   <FontAwesomeIcon icon={Item.icon} iconClass={'text-light'} />
                                                </div>
                                             </span>
                                             {Item.title}
                                          </h5>
                                          <p className="fs-8 ps-5 ms-3">{Item.text}</p>
                                       </div>
                                    ) : null}
                                 </div></React.Fragment> :

                                 <>
                                    <div className="w-100">
                                       <h4 className="border-bottom p-4 fs-6 mb-0">{pageContent?.manage_rules ?? ""}</h4>
                                    </div>

                                    <div className="d-flex flex-row border-bottom py-3 px-4">
                                       {pageContent?.rule_list?.length ? pageContent?.rule_list?.map((Item, index) => (
                                          <div key={index} className={`fw-semibold ${index === 3 ? 'text-end' : ''}`} style={{ width: `${index % 2 ? '15' : '35'}%` }}>
                                             {Item.heading}
                                          </div>
                                       )) : null}
                                    </div>

                                    <DragDropContext onDragEnd={this.onDragEnd}  >
                                       <Droppable droppableId="droppable" isDropDisabled={ruleList?.length < 2 || planId == 57 ? true : false}>
                                          {(provided, snapshot) => (
                                             <div {...provided.droppableProps} ref={provided.innerRef} >
                                                {ruleList?.map((rule, index) => (
                                                   <Draggable key={rule.id} draggableId={String(rule.id)} index={index}>
                                                      {(provided, snapshot) => (
                                                         <div ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps} style={getItemStyle(snapshot.isDragging, provided.draggableProps.style)} className='bg-body border-bottom'>
                                                            <div className="d-flex align-items-center flex-row py-2 px-4 pe-1">
                                                               <div className='d-flex p-1' style={{ width: '35%' }}>
                                                                  <FontAwesomeIcon icon={'grip-dots-vertical'} iconClass="me-2 mt-1 text-muted" />
                                                                  <span className='d-inline-block text-break text-dark'>{rule.name}</span>
                                                               </div>
                                                               <div className='p-1' style={{ width: '15%' }}>
                                                                  <Form.Check type="switch" checked={rule.status} onChange={() => planId == 57 ? planUpgrade(navigate) : this.changeStatus(rule.id, !rule.status)} disabled={isStatus} />
                                                               </div>
                                                               <div className='p-1' style={{ width: '35%' }}>
                                                                  {rule.lastExecuted ? moment.unix(rule.lastExecuted).format("DD MMM YYYY") : '--'}
                                                               </div>
                                                               <div className="d-flex align-items-center justify-content-end text-end" style={{ width: '15%' }}>

                                                                  {<OverlayTrigger placement="top" overlay={<Tooltip>{'Edit'}</Tooltip>}>
                                                                     <button id={`edit_${index}`} className='border-0 btn btn-icon btn-sm bg-transparent' style={{ background: '#F5F5F5' }} onClick={() => this.setState({ ruleData: rule, editId: rule.id }, () => {
                                                                        const timer7 = setTimeout(() => {
                                                                           this.setState({ ruleData: {} })
                                                                        }, 100); this.timeouts.push(timer7)
                                                                     })}>
                                                                        <FontAwesomeIcon icon={'pen-to-square'} iconClass="me-1 text-muted" />
                                                                     </button>
                                                                  </OverlayTrigger>}

                                                                  {<OverlayTrigger placement="top" overlay={<Tooltip>{'Delete'}</Tooltip>}>
                                                                     <button id={`delete_${index}`} className='border-0 btn btn-icon btn-sm bg-transparent' aria-label="delete" style={{ background: '#F5F5F5' }} onClick={() => planId == 57 ? planUpgrade(navigate) : this.confirmDelete(rule.id)} disabled={isDelete}>
                                                                        <FontAwesomeIcon icon={'trash-can'} iconClass="me-1 text-muted" />
                                                                     </button>
                                                                  </OverlayTrigger>}

                                                               </div>
                                                            </div>
                                                         </div>
                                                      )}
                                                   </Draggable>
                                                ))}
                                                {provided.placeholder}
                                             </div>
                                          )}
                                       </Droppable>
                                    </DragDropContext>

                                 </>}

                              <div className="w-100 mb-4 mt-5">
                                 <h4 className="border-bottom p-4 fs-6 mb-0">{pageContent?.default_rules_msg ?? ""}</h4>
                              </div>

                              <div className="px-5 rules-list">
                                 <div className="border rounded-2 p-4 mb-4">
                                    <div className="d-flex align-items-center justify-content-between mb-4">
                                       <h3 className="fs-7 mb-0">{pageContent?.read_to_use_ai_rules ?? ""}:</h3>
                                       <span style={{ verticalAlign: 'middle', fontStyle: 'italic', fontSize: '11px', color: 'rgb(153, 153, 153)', fontWeight: '400' }}>
                                          {window?.APP_CONTENT?.common?.powered_by ?? ""} <Image src={`${MediaBaseUrl}media/icons/svg-icons/true-sense.svg`} alt="TruSense" height="16" width="16" />
                                          <span style={{ verticalAlign: 'middle', fontSize: '13px', fontStyle: 'normal', color: 'var(--bs-gray-900)' }}> TruSense</span></span>
                                    </div>
                                    {pageContent?.default_rules?.length ? pageContent?.default_rules?.map((Item, index) =>
                                       <div className={`d-flex rounded-2 border border primary p-2 mb-3 cursor-pointer ${truSense || Item.type == 'Negativity Neutralizer' ? 'prefix_rule' : ''}`} style={{ background: 'var(--bs-gray-100)' }}>
                                          <div className="d-flex flex-column w-100">
                                             <h5 className="d-flex align-items-center justify-content-between fs-8 mb-0">
                                                <div className="d-flex align-items-center">
                                                   <div className={`f-center me-2 h-26px w-26px rounded-1 bg-dark`}>
                                                      <FontAwesomeIcon icon={Item.icon} iconClass={`text-light`} />
                                                   </div>
                                                   <span className="d-inline-block me-2">{Item.title}</span>
                                                   <OverlayTrigger placement='top' overlay={<Tooltip>{Item.text}</Tooltip>} >
                                                      <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'} /></span>
                                                   </OverlayTrigger>
                                                </div>
                                                {
                                                   truSense || Item.type == 'Negativity Neutralizer' ? 
                                                   <Form.Check type="switch" checked={ruleList?.map(item => item.name)?.includes(Item.title) || index == activeDefIndex - 1} onClick={(e) => e.target.checked ? this.setDefault(index + 1, Item.title) : this.setDefault(null, '')} disabled={ruleList?.map(item => item.name)?.includes(Item.title)} />:
                                                   <div className="">
                                                      <button className="btn btn-link text-primary text-nowrap p-0" type="button" onClick={() => navigate('/price')} >{'Upgrade Plan'}</button>
                                                   </div>
                                                }

                                             </h5>
                                          </div>
                                       </div>
                                    ) : null}
                                 </div>
                              </div>
                           </div>
                           <div className="d-flex flex-grow-1 position-relative">
                              <ManageRules defRule={defRule} defIndex={defIndex} modifyRule={this.modifyRule} ruleList={ruleList} ruleData={ruleData} reset={reset} spinner={spinner} editId={editId} editStatus={editStatus} editOrder={editOrder} navigate={navigate} planId={planId} ownerId={ownerId} pageContent={pageContent} />
                           </div>
                        </Card.Body>
                     </Card>
                  </Container>
               </div>
            </div>
         </div>
      </>
   }
}

const mapStateToProps = (state) => {
   return {
      allWalls: state?.dashboardData?.dashboarddata?.allWalls,
      manageFeeds: state.manageFeeds,
      userData: state.authorized?.userData?.user_data
   }
}

export default connect(mapStateToProps, null)(AutoPilot)