import React, { Component } from 'react';
import { Dropdown } from 'react-bootstrap';
import ReactHtmlParser from 'react-html-parser';
import BoxDesign from '../BoxDesign';
import { formatTemplateText, GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../utils';
import { baseUrl } from '../../../../../Assets/AssetsHelper';

class Tfeeds extends Component {
    constructor(){
        super();
        this.state = {
          feedList:[]
        }
    }

    timeoutId = null;

    componentDidMount(){
      const { feedList }= this.props
      this.timeoutId = setTimeout(() => {
        this.setState({feedList: feedList ?  feedList : []})
      }, 100);
    }
      
    componentWillReceiveProps(nextProps){
      const { feedList } = nextProps
      this.setState({feedList: feedList ? feedList : []})
    }

    componentWillUnmount(){
      if (this.timeoutId) clearTimeout(this.timeoutId);
    }

    onSearchFeed = (event) => {
      const results = this.props.feedList.filter(feed => feed.name.toLowerCase().includes(event.target.value.toLowerCase()))
      this.setState({feedList:results})
    }

    render(){
        const {handleTrigger, selectedFeeds, onSelectFeed, selectAllFeeds, pageContent } = this.props
        const {feedList} = this.state
        return <>
        <div className="border-dashed border-1 border-gray rounded-2 p-3">
          <BoxDesign name={pageContent?.feed_title ?? ""} desc={formatTemplateText(pageContent?.opt_desc ?? "", { section: pageContent?.feed_title ?? "" })} icon={"image-portrait"} method={()=>handleTrigger("feeds", "selectedFeeds", [])} svgIcon={true}/>
          <Dropdown className="w-100" autoClose="outside">
            <Dropdown.Toggle variant="outline-secondary" className="border-gray w-100 d-flex align-items-center">
              <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`filter_${selectedFeeds && selectedFeeds.length>0 ?GET_NETWORK_NAME(selectedFeeds[0].networkId):""}`}>
                <span className="form-check-label text-gray-800 text-ellipsis fw-medium ps-0">{selectedFeeds.length > 0 ? selectedFeeds.length==1 ? ReactHtmlParser(selectedFeeds[0].name) : formatTemplateText(pageContent?.selected_count ?? "", { count: selectedFeeds.length }) : formatTemplateText(pageContent?.opt_select ?? "", { section: pageContent?.feed_title ?? "" })}</span>
              </label>
            </Dropdown.Toggle>
            <Dropdown.Menu className="w-100 overflow-auto" style={{maxHeight:400}}>
                <div className="d-flex align-items-center px-3 cursor-pointer mb-2">
                  <label className="form-check flex-grow-1 fw-normal fs-8" htmlFor="tri-searchFeeds">
                  {pageContent?.opt_select_all ?? ""}
                  </label>
                <input className="form-check-input" type="checkbox" name="" value="" id="tri-searchFeeds" onChange={event=>selectAllFeeds(event)} checked={this.props.feedList.length == selectedFeeds.length && this.props.feedList.length!=0} />
              </div>
              <div className="form-group mb-2 px-3">
                <input type="text" className="form-control form-control-sm" placeholder={formatTemplateText(pageContent?.opt_search_ph ?? "", { section: "Feed..." })} onChange={event=> this.onSearchFeed(event)} />
              </div>
                {feedList && feedList.length > 0 ? feedList.map(feed => <div className='dropdown-item' key={feed.feedId}>
                  <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`tri-conn-${feed.feedId}`}>
                    <span className="symbol symbol-20">
                      <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(feed.networkId)}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(feed.networkId)} />
                    </span>
                    <span className="form-check-label text-gray-800 text-ellipsis fw-medium">{ReactHtmlParser(feed.name)}</span>
                  </label>
                  <input className="form-check-input flex-shrink-0" type="checkbox" name="" id={`tri-conn-${feed.id}`} checked={selectedFeeds ? selectedFeeds.some(fed => fed.id == feed.id) : ""} onChange={event => onSelectFeed(feed)} />
                </div>) : <div className="py-4">
                  <h6 className="text-center fs-9 mb-0 text-gray-700">{formatTemplateText(pageContent?.opt_no_results ?? "", { section: "Feeds" })}</h6>
                </div>}
            </Dropdown.Menu>
          </Dropdown>
        </div>
        <div className='action_gap'></div>
        </>
    }

}

export default Tfeeds