import React, { Component } from 'react'
import { Button, Card, Dropdown } from 'react-bootstrap';
import TriggerFilters from './TriggerFilters';
import TNetwork from './Triggers/TNetwork';
import TAi from './Triggers/TAi';
import Tfeeds from './Triggers/TFeeds';
import TMedia from './Triggers/TMedia';
import TTag from './Triggers/TTag';
import TShowHide from './Triggers/TShowHide';
import TDuplicate from './Triggers/TDuplicate';
import TSentiment from './Triggers/TSentiment';
import AutoActions from './AutoActions';
import { FontAwesomeIcon } from '../../../../Assets/AssetsHelper';
import { connect } from 'react-redux';
import { GET_ADD_FEED } from '../../../../actions/moderationActions';
import { GET_FEEDS, getLanguage } from '../../../../actions/manageFeedsAction';
import { getTags } from '../../../../actions/tagsActions';
import TProfanity from './Triggers/TProfanity';
import { OPEN_INTERCOM, shopifyApp, shopifyForm, SWAL_FIRE, TOAST_ERROR } from '../../../../utils';
import { isEqual } from 'lodash';
import { filtersList, planUpgrade } from './utils';
import TCommon from './Triggers/TCommon';
import { MediaBaseUrl, WASABI_PATH } from '../../../../actions/api';
import TLanguage from './Triggers/TLanguage';
import TRating from './Triggers/TRating';

class ManageRules extends Component {
    state = {
        ruleName: '',
        filterTabs: {},
        actionTabs: {},
        feedList: [],
        tagsList: [],
        selectedNetworks: [],
        selectedFeeds: [],
        selectedMedia: [],
        selectedTags: [],
        networkList: [],
        selectedVisibility: '',
        selectedActionTags: [],
        selectedActionVisibility: [],
        selectedAiType: [],
        selectedSentiment: [],
        selectedHandle: '',
        selectedHashtag: '',
        selectedKeyword: '',
        selectedDuplicateAsset: false,
        isActionSelected: false,
        isTriggerSelected: false,
        isError: false,
        ruleStatus: true,
        isEdit: false,
        loading: false,
        ruleId: null,
        prevRuleName: '',
        preProfanity: true,
        profName: '',
        // defaultProf: `${WASABI_PATH}app/assets/media/downloads/profanity.csv`,
        defaultProf: `${MediaBaseUrl}/media/downloads/profanity.csv`,
        order: null,
        isShow: false,
        handleError: false,
        keyError: false,
        hashError: false,
        isChanged: false,
        selectLanguage: [],
        selectedRating:[]

    };

    isShopifyApp = shopifyApp();
    timeouts = []

    getRuleData = () => {
        const { ruleData } = this.props
        const { feedList, defaultProf } = this.state
        var filterTabs = {}
        var actionTabs = {}
        Object.keys(ruleData).map(item => {
            if (ruleData[item]) {
                if (item == 'name') this.setState({ ruleName: ruleData[item], prevRuleName: ruleData[item] })
                if (filtersList.includes(item)) {
                    filterTabs[item] = true
                    if (item == 'feeds') {
                        const feeds = feedList ? feedList.filter(feed => ruleData[item].includes(feed.id)) : []
                        this.setState({ selectedFeeds: feeds })
                    }
                    if (item == 'networks') {
                        const networks = ruleData[item].split(',').map(Number);
                        this.setState({ selectedNetworks: networks })
                    }
                    if (item == 'assetsType') {
                        const types = []
                        if (ruleData[item].includes("1")) types.push("1")
                        if (ruleData[item].includes("3")) types.push("3,5")
                        if (ruleData[item].includes("2")) types.push("2,4")
                        this.setState({ selectedMedia: types })
                    }
                    if (item == 'recommend') this.setState({ selectedAiType: ruleData[item].split(',') })
                    if (item == 'sentiment') this.setState({ selectedSentiment: ruleData[item].split(',') })
                    if (item == 'showHide') this.setState({ selectedVisibility: String(ruleData[item]) })
                    if (item == 'tags') this.setState({ selectedTags: ruleData[item].split(",") })
                    if (item == 'handle') this.setState({ selectedHandle: ruleData[item] })
                    if (item == 'hashtag') this.setState({ selectedHashtag: ruleData[item] })
                    if (item == 'keyword') this.setState({ selectedKeyword: ruleData[item] })
                    if (item == 'duplicateAsset') this.setState({ selectedDuplicateAsset: true })
                    if (item == 'profanity') this.setState({ preProfanity: ruleData['profanity'] == defaultProf, profName: ruleData['profanity'] == defaultProf ? '' : ruleData['profanity'] })
                    if (item == 'language') {
                        const language = ruleData[item].split(',').map(String);
                        this.setState({ selectLanguage: language })
                    }
                    if (item == 'rating') {
                        const rating = ruleData[item].split(',').map(String);
                        this.setState({ selectedRating: rating })
                    }
                }
                if (item == 'actions') {
                    ruleData[item].map(action => {
                        if (action.type == 4) {
                            if(action.value == '1') actionTabs['public'] = true
                            else if(action.value == '2') actionTabs['private'] = true
                        }
                        if (action.type == 3) {
                            actionTabs["addTag"] = true
                            this.setState({ selectedActionTags: action.value.split(',') })
                        }
                        if (action.type == 5) {
                            actionTabs['delete'] = true
                        }
                    })
                }
            }
        })
        this.setState({ filterTabs: filterTabs, actionTabs: actionTabs, ruleName: ruleData['name'], ruleId: ruleData['id'], isActionSelected: true, isTriggerSelected: true, ruleStatus: ruleData.status, isEdit: true, order: ruleData['order'] })
    }

    handleTrigger = (name, type, val) => {
        var { filterTabs, handleError, keyError, hashError } = this.state
        if (filterTabs[name]) {
            delete filterTabs[name]
            this.setState({ filterTabs: filterTabs, [type]: val, hashError: name == 'hashtag' ? false : hashError, keyError: name == 'keyword' ? false : keyError, handleError: name == 'handle' ? false : handleError, isChanged: true, ...(type == 'profName' ? { preProfanity: true } : {}) }, () => this.enableAction())
        }
        else if (name == "duplicateAsset") {
            filterTabs[name] = true
            this.setState({ filterTabs: filterTabs, selectedDuplicateAsset: true, isChanged: true }, () => this.enableAction())
        }
        else {
            filterTabs[name] = true
            this.setState({ filterTabs: filterTabs, isChanged: true })
            if (name == 'profanity') this.enableAction()
        }

    }

    handleAction = (name, type, val) => {
        var { actionTabs } = this.state

        if (actionTabs[name]) delete actionTabs[name]
        else actionTabs[name] = true

        this.setState({ actionTabs: actionTabs, isChanged: true, ...(type && name != 'delete' ? { [type]: val } : {}) }, () => this.enableCreateRule())
    }

    setNetworks = () => {
        const { networks } = this.props
        this.setState({ networkList: networks.filter(network => ![33, 34, 35, 0].includes(network.id)) })
    }

    setFeeds = () => {
        const { feeds } = this.props
        this.setState({ feedList: feeds.filter(feed => feed.networkId != 0) })
    }

    enableAction = () => {
        const { selectedNetworks, selectedFeeds, selectedMedia, selectedTags, selectedVisibility, selectedHandle, selectedHashtag, selectedKeyword, selectedDuplicateAsset, selectedAiType, selectedSentiment, preProfanity, filterTabs, profName, handleError, keyError, hashError, selectedRating, selectLanguage } = this.state
        if ((selectedRating.length > 0 || selectLanguage.length > 0 || selectedNetworks.length > 0 || selectedFeeds.length > 0 || selectedMedia.length > 0 || selectedTags.length > 0 || selectedVisibility.length > 0 || selectedHandle || selectedKeyword || selectedHashtag || selectedDuplicateAsset || selectedAiType.length > 0 || selectedSentiment.length > 0 || (filterTabs.profanity && (preProfanity || profName))) && !handleError && !keyError && !hashError) { this.setState({ isTriggerSelected: true }) }
        else this.setState({ isTriggerSelected: false })
    }

    componentDidMount() {
        const { networks, getNetworks, feeds, getFeeds, getTags, tagsList, ownerId, languageData, getLanguage } = this.props
        if (!networks) getNetworks()
        else this.setNetworks()
        if (!feeds) getFeeds()
        else this.setFeeds()
        if (tagsList.length <= 0) getTags(ownerId)
        if (!languageData) getLanguage()
        if (this.isShopifyApp) {
            window.addEventListener('onSaveClicked', this.saveRule)
            window.addEventListener('onDiscardClicked', this.resetRule)
        }
    }

    componentDidUpdate(prevProps, prevState) {
        const { defRule, defIndex, ruleData, reset, editId, editStatus, editOrder } = this.props
        const { isTriggerSelected, isActionSelected, isChanged } = this.state
        if (prevProps.networks != this.props.networks) this.setNetworks()
        if (prevProps.feeds != this.props.feeds) this.setFeeds()
        if (prevProps.defRule == false && defRule == true && defIndex != 0) this.setDefRule(defIndex)
        if (editId == this.state.ruleId && editStatus != null && prevProps.editStatus != editStatus) this.setState({ ruleStatus: editStatus })
        if (editId == this.state.ruleId && editOrder != null && prevProps.editOrder != editOrder) this.setState({ order: editOrder })
        if (!isEqual(prevProps.ruleData, ruleData) && Object.keys(ruleData).length > 0) this.getRuleData()
        if (prevProps.reset == false && reset == true) this.resetRule()
        if (this.isShopifyApp) {
            if (prevState.isChanged != isChanged && isChanged) {
                shopifyForm()
                window.saveBar.set({ saveAction: { disabled: true } });
            }
            if (((prevState.isTriggerSelected != isTriggerSelected) || (prevState.isActionSelected != isActionSelected)) && isChanged) {
                if (isTriggerSelected && isActionSelected) window.saveBar.set({ saveAction: { disabled: false } });
                else window.saveBar.set({ saveAction: { disabled: true } });
            }
        }
    }

    componentWillUnmount() {
        if (this.isShopifyApp) {
            window.removeEventListener('onSaveClicked', this.saveRule)
            window.removeEventListener('onDiscardClicked', this.resetRule)
        }
        this.timeouts.forEach((timeout) => clearTimeout(timeout));
        this.timeouts = []; // Reset the array
    }

    setDefRule = (index) => {
        const { isEdit } = this.state
        if (index == 1) {
            this.resetRule(isEdit)
            const timer1 = setTimeout(() => {
                this.setState({ filterTabs: { duplicateAsset: true }, selectedDuplicateAsset: true, isTriggerSelected: true, actionTabs: { private: true }, isActionSelected: true, ruleName: 'Bye-bye Duplicates', isChanged: true })
            }, 10);
            this.timeouts.push(timer1)
        }
        else if (index == 2) {
            this.resetRule(isEdit)
            const timer2 = setTimeout(() => {
                this.setState({ filterTabs: { sentiment: true }, selectedSentiment: ['3'], isTriggerSelected: true, actionTabs: { private: true }, isActionSelected: true, ruleName: 'Negativity Neutralizer', isChanged: true })
            }, 10);
            this.timeouts.push(timer2)
        }
        else if (index == 3) {
            this.resetRule(isEdit)
            const timer3 = setTimeout(() => {
                this.setState({ filterTabs: { recommend: true }, selectedAiType: ['1'], isTriggerSelected: true, actionTabs: { public: true }, isActionSelected: true, ruleName: 'AI Content Spotlight', isChanged: true })
            }, 10);
            this.timeouts.push(timer3)
        }

        else if (index == null) {
            this.resetRule(isEdit)
            const timer3 = setTimeout(() => {
                this.setState({ filterTabs: {}, selectedAiType: [], isTriggerSelected: false, actionTabs: {}, isActionSelected: false, ruleName: '', isChanged: false })
            }, 10);
            this.timeouts.push(timer3)
        }

    }

    onSelectLanguage = (data) => {
        let { selectLanguage } = this.state

        if (selectLanguage.includes(data)) {
            selectLanguage = selectLanguage.filter((val) => val !== data);
        }

        else selectLanguage.push(data);

        this.setState({ selectLanguage: selectLanguage, isChanged: true }, () => this.enableAction())
        
    }

    selectAllLanguage = (event) => {
        const { languageData } = this.props
        var updatedNetworks = []
        if (event.target.checked) {
            updatedNetworks = languageData.map(network => network.language)
        }
        this.setState({ selectLanguage: updatedNetworks, isChanged: true }, () => this.enableAction())
    }
        
    onSelectRating = (id) => {
        let { selectedRating } = this.state
        if (selectedRating.includes(id)) { selectedRating = selectedRating.filter( (val) => val !== id )}
        else selectedRating.push(id);
        this.setState({ selectedRating, isChanged: true }, () => this.enableAction())
    }

    selectAllRating = (event) => {
        var updatedNetworks = []
        if (event.target.checked)   updatedNetworks = ['5','4','3','2','1']
        this.setState({ selectedRating: updatedNetworks, isChanged: true }, () => this.enableAction())
    }

    onSelectNetwork = (id) => {
        let { selectedNetworks } = this.state

        if (selectedNetworks.includes(id)) {
            selectedNetworks = selectedNetworks.filter(
                (networkId) => networkId !== id && (id !== 18 || networkId !== 2)
            );
        }

        else selectedNetworks.push(id, ...(id === 18 ? [2] : []));

        this.setState({ selectedNetworks: selectedNetworks, isChanged: true }, () => this.enableAction())
    }

    selectAllNetworks = (event) => {
        const { networkList } = this.state
        var updatedNetworks = []
        if (event.target.checked) {
            updatedNetworks = networkList.map(network => network.id)
            updatedNetworks.push(2)
        }
        this.setState({ selectedNetworks: updatedNetworks, isChanged: true }, () => this.enableAction())
    }

    onChangeRuleName = event => {
        if (event.target.value == '') this.setState({ ruleName: event.target.value, isError: true })
        else this.setState({ ruleName: event.target.value, isError: false, isChanged: true })
    }

    onChangeRuleStatus = event => this.setState({ ruleStatus: !this.state.ruleStatus, isChanged: true })

    onSelectFeed = (feed) => {
        let { selectedFeeds } = this.state
        if (selectedFeeds.includes(feed)) selectedFeeds = selectedFeeds.filter(feeds => feeds != feed)
        else selectedFeeds.push(feed)
        this.setState({ selectedFeeds: selectedFeeds, isChanged: true }, () => this.enableAction())
    }

    selectAllFeeds = (event) => {
        const { feedList } = this.state
        this.setState({ selectedFeeds: event.target.checked ? feedList : [], isChanged: true }, () => this.enableAction())
    }

    onSelectMediaType = (type) => {
        let { selectedMedia } = this.state
        if (selectedMedia.includes(type)) selectedMedia = selectedMedia.filter(asset => asset != type)
        else selectedMedia.push(type)
        this.setState({ selectedMedia: selectedMedia, isChanged: true }, () => this.enableAction())
    }

    selectAllMediaTypes = (event) => this.setState({ selectedMedia: event.target.checked ? ['1', '2,4', '3,5'] : [], isChanged: true }, () => this.enableAction())

    onSelectAiType = (type) => {
        let { selectedAiType } = this.state
        if (selectedAiType.includes(type)) selectedAiType = selectedAiType.filter(ai => ai != type)
        else selectedAiType.push(type)
        this.setState({ selectedAiType: selectedAiType, isChanged: true }, () => this.enableAction())
    }

    selectAllAiTypes = (event) => this.setState({ selectedAiType: event.target.checked ? ['1', '2', '3'] : [], isChanged: true },
        () => this.enableAction())

    onSelectSentiment = (type) => {
        let { selectedSentiment } = this.state
        if (selectedSentiment.includes(type)) selectedSentiment = selectedSentiment.filter(score => score != type)
        else selectedSentiment.push(type)
        this.setState({ selectedSentiment: selectedSentiment, isChanged: true }, () => this.enableAction())
    }

    selectAllSentiments = (event) => this.setState({ selectedSentiment: event.target.checked ? ['1', '2', '3'] : [], isChanged: true },
        () => this.enableAction())

    onSelectTag = (tag, type) => {
        const { selectedTags, selectedActionTags } = this.state
        if (type == "filter") {
            selectedTags.push(tag.value)
            this.setState({ selectedTags, isChanged: true }, () => this.enableAction())
        }
        else {
            selectedActionTags.push(tag.value)
            this.setState({ selectedActionTags, isChanged: true }, () => this.enableCreateRule())
        }
    }

    onTagRemove = (index, type) => event => {
        let { selectedTags, selectedActionTags } = this.state;
        if (type == "filter") {
            selectedTags.splice(index, 1)
            this.setState({ selectedTags, isChanged: true }, () => this.enableAction())
        }
        else {
            selectedActionTags.splice(index, 1)
            this.setState({ selectedActionTags, isChanged: true }, () => this.enableCreateRule())
        }
    }

    onSelectVisibility = (value, type) => {
        if (type == "filter") this.setState({ selectedVisibility: value, isChanged: true }, () => this.enableAction())
        else this.setState({ selectedActionVisibility: value, isChanged: true }, () => this.enableCreateRule())
    }

    onChangeHandle = (event) => this.setState({ selectedHandle: event.target.value, handleError: String(event.target.value).length < 2 ? true : false, isChanged: true }, () => this.enableAction())

    onChangeHashtag = (event) => this.setState({ selectedHashtag: event.target.value, hashError: String(event.target.value).length < 2 ? true : false, isChanged: true }, () => this.enableAction())

    onChangeKeyword = (event) => this.setState({ selectedKeyword: event.target.value, keyError: String(event.target.value).length < 3 ? true : false, isChanged: true }, () => this.enableAction())

    onChangeProfanity = () => this.setState(prevState => ({ preProfanity: !prevState.preProfanity, isChanged: true }), () => this.enableAction())

    setProfanity = (val) => this.setState({ profName: val, isChanged: true }, () => this.enableAction())

    enableCreateRule = () => {
        const { selectedActionTags, selectedActionVisibility, isTriggerSelected, actionTabs } = this.state
        if (isTriggerSelected && (selectedActionTags.length > 0 || actionTabs['public'] || actionTabs['delete'] || actionTabs['private'])) this.setState({ isActionSelected: true })
        else this.setState({ isActionSelected: false })
    }

    confirmReset = () => {
        SWAL_FIRE({
            title: `Are you sure?`,
            text: `All changes will be removed.`,
            icon: 'warning',
            showCancelButton: true,
            cancelButtonText: "Cancel",
            confirmButtonText: "Reset",
            confirmButtonColor: `var(--bs-success)`,

        }).then((result) => {
            if (result.value) {
                this.resetRule()
            }
            else if (result.dismiss === "cancel") { }
        });
    }

    resetRule = (isRule) => {
        this.setState({
            ruleName: '',
            filterTabs: {}, actionTabs: {}, selectedNetworks: [], selectLanguage:[],
            selectedFeeds: [], selectedMedia: [], selectedTags: [],
            selectedVisibility: '', selectedActionTags: [], selectedActionVisibility: [],
            selectedAiType: [], selectedSentiment: [], selectedHandle: '',
            selectedHashtag: '', selectedKeyword: '', selectedDuplicateAsset: false,
            isActionSelected: false, isTriggerSelected: false, isError: false,
            ruleStatus: true, isEdit: isRule ? true : false, loading: false,
            ruleId: isRule ? this.state.ruleId : null, prevRuleName: isRule ? this.state.prevRuleName : '', preProfanity: true, profName: '',
            defaultProf: `${WASABI_PATH}app/assets/media/downloads/profanity.csv`,
            isChanged: false
        })
        if (this.isShopifyApp) shopifyForm('hide')
    }

    saveRule = () => {
        const { planId, navigate } = this.props
        if (planId == 57) {
            shopifyForm('hide')
            planUpgrade(navigate)
        }
        else {
            window.saveBar.set({ saveAction: { loading: true } })
            this.onSubmitData()
        }
    }

    onSubmitData = () => {
        const { ruleName, isEdit, prevRuleName } = this.state
        const { ruleList, modifyRule } = this.props
        if (ruleName.length > 0) {
            const existsRuleName = ruleList && ruleList?.length > 0 ? ruleList.filter(rule => String(rule.name).toLowerCase() == String(ruleName).toLowerCase()) : [];
            if (existsRuleName && existsRuleName.length > 0 && (isEdit ? existsRuleName[0].name != prevRuleName : true)) TOAST_ERROR("Rule name already exists. ")
            else {
                const payload = this.createPayload()
                modifyRule(payload, false, () => this.setState({ isChanged: false, ...(isEdit ? { prevRuleName: ruleName } : {}) }))
            }
        }
        else this.setState({ isError: true })
    }

    createPayload = () => {
        const { actionTabs, filterTabs, ruleName, selectedFeeds, selectedNetworks, selectedTags, selectedVisibility, selectedMedia, selectedActionTags, selectedHandle, selectedHashtag, selectedKeyword, ruleStatus, selectedDuplicateAsset, selectedAiType, selectedSentiment, isEdit, ruleId, preProfanity, profName, defaultProf, order, selectLanguage, selectedRating } = this.state
        const { ruleList } = this.props

        let feed = []
        let network = []
        let tags = []
        let assetType = []
        let showHide = ''
        let actions = []
        let handle = ''
        let hashtag = ''
        let keyword = ''
        let duplicateAsset = 0
        let recommend = []
        let sentiment = []
        let profanity = ''
        let language = []
        let ratings= []

        Object.keys(filterTabs).map((filter) => {

            if (filter == 'rating' && selectedRating.length > 0) selectedRating.map(item => ratings.push(item))

            if (filter == 'feeds' && selectedFeeds.length > 0) selectedFeeds.map(item => feed.push(item.id))

            if (filter == 'networks' && selectedNetworks.length > 0) selectedNetworks.map(item => network.push(item))

            if (filter == 'language' && selectLanguage.length > 0) selectLanguage.map(item => language.push(item))

            if (filter == 'tags' && selectedTags.length > 0) selectedTags.map(item => tags.push(item))

            if (filter == 'assetsType' && selectedMedia.length > 0) selectedMedia.map(item => assetType.push(item))

            if (filter == 'showHide' && selectedVisibility.length > 0) showHide = selectedVisibility

            if (filter == 'handle' && selectedHandle) handle = selectedHandle

            if (filter == 'hashtag' && selectedHashtag) hashtag = selectedHashtag

            if (filter == 'keyword' && selectedKeyword) keyword = selectedKeyword

            if (filter == 'duplicateAsset' && selectedDuplicateAsset) duplicateAsset = 1

            if (filter == 'recommend' && selectedAiType.length > 0) selectedAiType.map(item => recommend.push(item))

            if (filter == 'sentiment' && selectedSentiment.length > 0) selectedSentiment.map(item => sentiment.push(item))

            if (filter == 'profanity' && (preProfanity || profName)) profanity = preProfanity ? defaultProf : profName
        })

        Object.keys(actionTabs).map(action => {
            let list = ''
            if (action == 'addTag' && selectedActionTags.length > 0) {
                selectedActionTags.map(tag => list += tag + ',')
                list = list.slice(0, -1)
                actions.push({ type: 3, value: list })
            }
            if (action == 'public') actions.push({ type: 4, value: '1' })
            if (action == 'private') actions.push({ type: 4, value: '2' })
            if (action == 'delete') actions.push({ type: 5, value: '' })

        })
        let payloadData = {
            name: ruleName,
            status: ruleStatus ? 1 : 0,
            feeds: String(feed),
            networks: String(network),
            tags: String(tags),
            assetsType: String(assetType),
            showHide: Number(showHide),
            handle: handle,
            hashtag: hashtag,
            keyword: keyword,
            duplicateAsset: duplicateAsset,
            recommend: String(recommend),
            sentiment: String(sentiment),
            profanity: String(profanity),
            actions,
            actionsType: 1,
            order: ruleList.length,
            language: String(language),
            rating: String(ratings)
        }

        payloadData = isEdit ? { ...payloadData, id: ruleId, actionsType: 2, order: order } : payloadData

        return payloadData

    }

    render() {
        const { filterTabs, actionTabs, ruleName, isError, ruleStatus, isEdit, duplicateAssets, networkList, feedList, preProfanity, defaultProf, profName, isTriggerSelected, isActionSelected, isShow, handleError, keyError, hashError, isChanged, selectedRating } = this.state;
        const { tagsList, spinner, planId, navigate, languageData, pageContent } = this.props
        return <>
            <div className='d-flex flex-column flex-grow-1 justify-content-between mb-10 pb-7 overflow-auto rule_action mt-5 mt-lg-0'>
                <div className="mb-8 mx-auto w-100 py-lg-10 py-4 px-4" style={{ maxWidth: '350px' }}>
                    <div className="mb-2 w-100">
                        <input type="text" className={`form-control ${isError ? 'is-invalid' : ''}`} placeholder={pageContent?.rule_name_ph ?? ""} onChange={this.onChangeRuleName} value={ruleName} />
                        {isError && <div className="invalid-feedback d-block">{pageContent?.rule_name_ph ?? ""}</div>}
                    </div>
                    <label className="form-check form-switch mb-3 fw-medium mb-8">
                        <input className="form-check-input me-2" type="checkbox" checked={ruleStatus} onChange={this.onChangeRuleStatus} />
                        {pageContent?.rule_status_label ?? ""}
                    </label>

                    {!(filterTabs && Object.keys(filterTabs).length > 0) ? <Dropdown className="w-100 add_rule">
                        <Dropdown.Toggle variant="outline-secondary" id="rule-dd" className="border border-gray border-1 w-100 p-4 arrow_disabled rounded-2">
                            <FontAwesomeIcon icon={'plus'} iconClass="text-muted" />
                            <span className="fs-9 fw-medium">{pageContent?.add_trigger ?? ""}</span>
                        </Dropdown.Toggle>
                        <TriggerFilters handleTrigger={this.handleTrigger} filterTabs={filterTabs} duplicateAssets={duplicateAssets} /></Dropdown> : null}

                    {filterTabs && Object.keys(filterTabs).map((filter) => {
                        if (filter == "language") return <TLanguage key={filter} handleTrigger={this.handleTrigger} selectLanguage={this.state.selectLanguage} onSelectLanguage={this.onSelectLanguage} languageList={languageData} selectAllLanguage={this.selectAllLanguage} pageContent={pageContent} />
                        else if (filter == "rating") return <TRating key={filter} handleTrigger={this.handleTrigger} selectedRating={selectedRating} onSelectRating={this.onSelectRating} selectAllRating={this.selectAllRating} pageContent={pageContent} />
                        else if (filter == "networks") return <TNetwork key={filter} handleTrigger={this.handleTrigger} selectedNetworks={this.state.selectedNetworks} onSelectNetwork={this.onSelectNetwork} networkList={networkList} selectAllNetworks={this.selectAllNetworks} pageContent={pageContent} />
                        else if (filter == "feeds") return <Tfeeds key={filter} handleTrigger={this.handleTrigger} selectedFeeds={this.state.selectedFeeds} onSelectFeed={this.onSelectFeed} feedList={feedList} selectAllFeeds={this.selectAllFeeds} pageContent={pageContent} />
                        else if (filter == "assetsType") return <TMedia handleTrigger={this.handleTrigger} selectedMedia={this.state.selectedMedia} onSelectMediaType={this.onSelectMediaType} selectAllMediaTypes={this.selectAllMediaTypes} pageContent={pageContent} />
                        else if (filter == "tags") return <TTag key={filter} handleTrigger={this.handleTrigger} selectedTags={this.state.selectedTags} onSelectTag={this.onSelectTag} onTagRemove={this.onTagRemove} tagsList={tagsList} pageContent={pageContent} />
                        else if (filter == "showHide") return <TShowHide key={filter} handleTrigger={this.handleTrigger} onSelectVisibility={this.onSelectVisibility} selectedVisibility={this.state.selectedVisibility} pageContent={pageContent} />
                        else if (filter == 'handle') return <TCommon key={filter} filter={filter} handleTrigger={this.handleTrigger} method={this.onChangeHandle} selectedVal={this.state.selectedHandle} isError={handleError} name={'Handle'} icon={'at'} pageContent={pageContent} />
                        else if (filter == 'hashtag') return <TCommon key={filter} filter={filter} handleTrigger={this.handleTrigger} method={this.onChangeHashtag} selectedVal={this.state.selectedHashtag} isError={hashError} name={'Hashtag'} icon={'hashtag'} pageContent={pageContent} />
                        else if (filter == 'keyword') return <TCommon key={filter} filter={filter} handleTrigger={this.handleTrigger} method={this.onChangeKeyword} selectedVal={this.state.selectedKeyword} isError={keyError} name={'Keyword'} icon={'text'} pageContent={pageContent} />
                        else if (filter == 'duplicateAsset') return <TDuplicate key={filter} handleTrigger={this.handleTrigger} duplicateAssets={duplicateAssets} pageContent={pageContent} />
                        else if (filter == "recommend") return <TAi handleTrigger={this.handleTrigger} selectedAiType={this.state.selectedAiType} onSelectAiType={this.onSelectAiType} selectAllAiTypes={this.selectAllAiTypes} pageContent={pageContent} />
                        else if (filter == 'sentiment') return <TSentiment handleTrigger={this.handleTrigger} selectedSentiment={this.state.selectedSentiment} onSelectSentiment={this.onSelectSentiment} selectAllSentiments={this.selectAllSentiments} pageContent={pageContent} />
                        else if (filter == 'profanity') return <TProfanity handleTrigger={this.handleTrigger} onChangeProfanity={this.onChangeProfanity} preProfanity={preProfanity} defaultProf={defaultProf} profName={profName} setProfanity={this.setProfanity} pageContent={pageContent} />
                        else return null
                    })}

                    {filterTabs && Object.keys(filterTabs).length > 0 && Object.keys(filterTabs).length < 13 ?
                        <Dropdown className="w-100 text-center add_more_rule" show={isShow} onToggle={event => this.setState({ isShow: Object.keys(filterTabs).length == 12 ? false : event })}>
                            <Dropdown.Toggle variant="outline-secondary" id="rule-dd" className="p-0 border-0 arrow_disabled d-flex m-auto">
                                <FontAwesomeIcon type={'light'} icon={'circle-plus'} iconClass="text-muted me-0 fs-4" />
                            </Dropdown.Toggle>
                            {<TriggerFilters handleTrigger={this.handleTrigger} filterTabs={filterTabs} duplicateAssets={duplicateAssets} />}
                        </Dropdown> : null}

                    <AutoActions actionTabs={actionTabs} handleAction={this.handleAction} isTriggerSelected={this.state.isTriggerSelected} selectedActionTags={this.state.selectedActionTags} onSelectTag={this.onSelectTag} onTagRemove={this.onTagRemove} tagsList={tagsList} selectedVisibility={this.state.selectedVisibility} pageContent={pageContent} />

                </div>
                <div className='p-4 bg-body position-sticky bottom-0'>
                    <div className='my-0 border border-warning alert alert-warning'>
                        {pageContent?.ai_note ?? ""}
                    </div>
                </div>
                {!this.isShopifyApp && <Card.Footer className='f-center justify-content-end position-absolute bottom-0 start-0 w-100 py-4 px-4 px-xl-6 bg-body'>
                    <div className="block-center d-flex align-items-center justify-content-between w-100">
                        {/*  */}
                        <div>
                            <span className="d-flex align-items-center text-muted py-1 px-3 rounded-pill badge-light-primary">
                                {pageContent?.autopilot_help ?? ""}
                                <span className="cursor-pointer f-center ms-1 d-flex btn btn-link" onClick={OPEN_INTERCOM}>{pageContent?.talk_us ?? ""} <strong className="ms-1">24/7</strong></span>
                            </span>
                        </div>
                        {/*  */}
                        {/* <Button onClick={()=>isChanged ? this.confirmReset() : this.resetRule()} className="btn btn-secondary btn-min-w me-3">Cancel</Button> */}
                        <Button type="button" className={`btn btn-primary btn-min-w ${spinner ? 'spinner' : ''}`} disabled={!(isTriggerSelected && isActionSelected)} onClick={() => planId == 57 ? planUpgrade(navigate) : this.onSubmitData()}>{isEdit ? pageContent?.update_btn ?? "" : pageContent?.create_btn ?? ""}</Button>
                    </div>
                </Card.Footer>}
            </div>
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        feeds: state.manageFeeds?.feeds,
        networks: state.addFeedReducer?.addFeedNetworks?.networks,
        tagsList: state.tags?.tagsList,
        languageData: state.manageFeeds?.languageData,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getNetworks: () => dispatch(GET_ADD_FEED()),
        getFeeds: () => dispatch(GET_FEEDS()),
        getTags: (ownerId) => dispatch(getTags(ownerId)),
        getLanguage: () => dispatch(getLanguage())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ManageRules)
