import { formatTemplateText } from '../../../../../utils';
import BoxDesign from '../BoxDesign';

const TDuplicate = ({ handleTrigger, duplicateAssets, pageContent }) => <>
    <div className="border-dashed border-1 border-gray rounded-2 p-3">
      <BoxDesign name={pageContent?.duplicate_assets ?? ""} icon={"clone"} method={()=>handleTrigger("duplicateAsset", "selectedDuplicateAsset", false)}
      desc={parseInt(duplicateAssets) > 0 ? formatTemplateText(pageContent?.selected_count ?? "", { count: `${parseInt(duplicateAssets)} ${pageContent?.posts}` }) : formatTemplateText(pageContent?.opt_select ?? "", { section: pageContent?.posts })} del={true}/>
    </div>
    <div className='action_gap'></div>
  </>

export default TDuplicate