import { Dropdown } from 'react-bootstrap';
import BoxDesign from '../BoxDesign';
import { formatTemplateText } from '../../../../../utils';

const TAi = ({handleTrigger, selectedAiType, onSelectAiType, selectAllAiTypes, pageContent}) => <>
    <div className="border-dashed border-1 border-gray rounded-2 p-3">
      <BoxDesign name={pageContent?.ai_recommendation ?? ""} desc={pageContent?.choose_ai_recommendation ?? ""} icon={"sparkles"} method={()=>handleTrigger("recommend", "selectedAiType", [])}/>
      <Dropdown className="w-100" autoClose="outside">
        <Dropdown.Toggle variant="outline-secondary" className="border-gray w-100 d-flex align-items-center">
          <div className="d-flex align-items-center flex-grow-1">
            {selectedAiType && selectedAiType.length== 1 ? 
              <span className={`h-10px w-10px rounded-circle me-2 bg-${selectedAiType.includes("1") ? "success" : selectedAiType.includes("2") ? "warning" : "danger"}`}></span>
            : null}
            <p className="fs-9 fw-medium mb-0">{selectedAiType.length > 0 ? formatTemplateText(pageContent?.selected_count ?? "", { count: selectedAiType.length==1 ? selectedAiType.includes("1") ? pageContent?.recommended : selectedAiType.includes("2") ? pageContent?.considerable : pageContent?.not_recommend :`${selectedAiType.length}` }) : formatTemplateText(pageContent?.opt_select ?? "", { section: `${pageContent?.ai_recommendation ?? ""} ${pageContent?.type ?? ""}` })}</p>
          </div>
        </Dropdown.Toggle>
        <Dropdown.Menu className="w-100">
          <div className="d-flex align-items-center px-3 cursor-pointer mb-2">
            <label className="form-check flex-grow-1 fw-normal fs-8" htmlFor="tri-MediaType">
          {pageContent?.opt_select_all ?? ""}
            </label>
            <input className="form-check-input" type="checkbox" name="" id="tri-MediaType" onChange={event=>selectAllAiTypes(event)} checked={selectedAiType.length==3} />
          </div>
          <div className='mb-1 dropdown-item'>
            <label className="form-check flex-grow-1 align-items-center" htmlFor="recommend">
              <span className="h-10px w-10px rounded-circle me-2 bg-success"></span>
              <p className="fs-9 fw-medium mb-0">{pageContent?.recommended ?? ""}</p>
            </label>
            <input className="form-check-input" type="checkbox" name="" value={'image'} id="recommend" onChange={event=>onSelectAiType("1")} checked={selectedAiType.includes("1")} />
          </div>
          <div className='mb-1 dropdown-item'>
            <label className="form-check flex-grow-1 align-items-center" htmlFor="considerable">
              <span className="h-10px w-10px rounded-circle me-2 bg-warning"></span>
              <p className="fs-9 fw-medium mb-0">{pageContent?.considerable ?? ""}</p>
            </label>
            <input className="form-check-input" type="checkbox" name="" value={'video'} id="considerable" onChange={event=>onSelectAiType("2")} checked={selectedAiType.includes("2")} />
          </div>
          <div className='mb-1 dropdown-item'>
            <label className="form-check flex-grow-1 align-items-center" htmlFor="not_recommend">
              <span className="h-10px w-10px rounded-circle me-2 bg-danger"></span>
              <p className="fs-9 fw-medium mb-0">{pageContent?.not_recommend ?? ""}</p>
            </label>
            <input className="form-check-input" type="checkbox" name="" value={'text'} id="not_recommend" onChange={event=>onSelectAiType("3")} checked={selectedAiType.includes("3")} />
          </div>
        </Dropdown.Menu>
      </Dropdown>
    </div>
    <div className='action_gap'></div>
  </>

export default TAi