import React, { Component } from 'react';
import { Dropdown } from 'react-bootstrap';
import BoxDesign from '../BoxDesign';
import Rating from '../../CommonComponents/Rating';
import { formatTemplateText } from '../../../../../utils';

class TRating extends Component {

  render() {
    const { handleTrigger, selectedRating, onSelectRating, selectAllRating, pageContent } = this.props
    return <>
      <div className="border-dashed border-1 border-gray rounded-2 p-3">
        <BoxDesign name={pageContent?.rating_title ?? ""} desc={formatTemplateText(pageContent?.opt_desc ?? "", { section: pageContent?.rating_title ?? "" })} icon={"chart-network"} svgIcon={true} method={()=>handleTrigger("rating", "selectedRating", [])} />
        <Dropdown className="w-100" autoClose="outside">
          <Dropdown.Toggle variant="outline-secondary" className="border-gray w-100 d-flex align-items-center">
            <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`filter_${selectedRating[0]}`}>
              <span className="form-check-label text-gray-800 text-ellipsis fw-medium ps-0">{selectedRating.length > 0 ? formatTemplateText(pageContent?.selected_count ?? "", { count: selectedRating.length }) : formatTemplateText(pageContent?.opt_select ?? "", { section: pageContent?.rating_title ?? "" })}</span>
            </label>
          </Dropdown.Toggle>
          <Dropdown.Menu className="w-100 overflow-auto" style={{ maxHeight: 400 }}>
            <div className="d-flex align-items-center px-3 cursor-pointer mb-2">
              <label className="form-check flex-grow-1 fw-normal fs-8" htmlFor="tri-searchNetwork">
                {pageContent?.opt_select_all ?? ""}
              </label>
              <input className="form-check-input" type="checkbox" id="tri-searchNetwork" onChange={event => selectAllRating(event)} checked={selectedRating.length == 5} />
            </div>
            {[...Array(5)].map((network, index) => <div className='dropdown-item' key={index}>
              <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`tri-network-${index}}`}>
                <Rating rating={5-index} />
                <span className="form-check-label text-gray-800 text-ellipsis fw-medium ps-0">{`${5-index} Star`}</span>
              </label>
              <input className="form-check-input flex-shrink-0" type="checkbox" id={`tri-network-${index}`} checked={selectedRating.includes(`${5-index}`)} onChange={event => {onSelectRating(`${5-index}`)}} />
            </div>)}
          </Dropdown.Menu>
        </Dropdown>
      </div>
      <div className='action_gap'></div>
    </>
  }
}

export default TRating