import { GET_LOCAL, SWAL_FIRE } from "../../../../utils";

export const ruleFilters = ({ duplicateAssets, handleTrigger }) => {
    const rule_filters = window?.APP_CONTENT?.auto_pilot?.rule_filters ?? [];
    const ownerId = GET_LOCAL("ownerId");

    return rule_filters.filter((filter) => filter.propName === "language" && ![1752756].includes(ownerId) ? false : true).map((filter) => {
        // Dynamic duplicateAsset description
        let desc = filter.desc;
        if (filter.propName === "duplicateAsset") {
            desc = parseInt(duplicateAssets) > 0 ? `${parseInt(duplicateAssets)} Select Posts` : "Select Posts";
        }
        return {
            ...filter,
            desc,
            fnc: () => handleTrigger(filter.propName),
        };
    });
}

export const filtersList = ['feeds', 'networks', 'handle', 'hashtag', 'keyword', 'showHide', 'tags', 'assetsType', 'duplicateAsset', 'sentiment', 'recommend', 'profanity', 'language', 'rating']

export const ruleActnFilters = (handleAction) => {
    const rule_actions = window?.APP_CONTENT?.auto_pilot?.rule_actions ?? [];
    return rule_actions.map((action) => {
        return {
            ...action,
            fnc: () => handleAction(action.propName),
        };
    })
}

export const planUpgrade = (navigate) => {
    SWAL_FIRE({
        title: `Upgrade to create rules`,
        text: `Oops! Looks like you need to upgrade your plan in order to create or modify rules.`,
        icon: 'error',
        showCancelButton: true,
        cancelButtonText: "Cancel",
        confirmButtonText: "Upgrade now!",
        confirmButtonColor:`var(--bs-success)`,

    }).then((result) => {
        if (result.value) {
            navigate('/price')
        }
        else if (result.dismiss === "cancel") { }
    });
}
