import React, { Component } from 'react';
import { Dropdown } from 'react-bootstrap';
import BoxDesign from '../BoxDesign';
import { formatTemplateText } from '../../../../../utils';

class TLanguage extends Component {

    state = {
        languageList: []
    }
  componentDidMount() {
      setTimeout(() => {
        this.setState({ languageList: this.props.languageList })
      }, 100);
  }

  componentWillReceiveProps(nextProps) {
    this.setState({ languageList: nextProps.languageList })
  }

  onSearchLanguage = (event) => {
    const { languageList } = this.props
    const results = languageList.filter(val => val.language.toLowerCase().includes(event.target.value.toLowerCase()))
    this.setState({ languageList: results })
  }

  render() {
    const { handleTrigger, selectLanguage, onSelectLanguage, selectAllLanguage, pageContent } = this.props
    const  {languageList}  = this.state;

    return <>
      <div className="border-dashed border-1 border-gray rounded-2 p-3">
        <BoxDesign name={pageContent?.language_title ?? ""} desc={formatTemplateText(pageContent?.opt_desc, { section: pageContent?.language_title ?? "" })} icon={"chart-network"} svgIcon={true} method={()=>handleTrigger("language", "selectLanguage", [])} />
        <Dropdown className="w-100" autoClose="outside">
          <Dropdown.Toggle variant="outline-secondary" className="border-gray w-100 d-flex align-items-center">
            <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`filter_${selectLanguage?.[0]?.language}`}>
              <span className="form-check-label text-gray-800 text-ellipsis fw-medium ps-0">{selectLanguage?.length > 0 ? selectLanguage?.length == 1 ? selectLanguage?.[0] : formatTemplateText(pageContent?.selected_count, { count: selectLanguage?.length }) : formatTemplateText(pageContent?.opt_select, { section: pageContent?.language_title ?? "" })}</span>
            </label>
          </Dropdown.Toggle>
          <Dropdown.Menu className="w-100 overflow-auto" style={{ maxHeight: 400 }}>
            <div className="d-flex align-items-center px-3 cursor-pointer mb-2">
              <label className="form-check flex-grow-1 fw-normal fs-8" htmlFor="tri-searchNetwork">
                {pageContent?.opt_select_all ?? ""}
              </label>
              <input className="form-check-input" type="checkbox" id="tri-searchNetwork" onChange={event => selectAllLanguage(event)} />
            </div>
            <div className="form-group mb-2">
              <input type="text" className="form-control form-control-sm" placeholder={formatTemplateText(pageContent?.opt_search_ph, { section: "language" })} onChange={event => this.onSearchLanguage(event)} />
            </div>
            {languageList && languageList.length > 0 ? languageList.map(val => <div className='dropdown-item' key={val.language}>
              <label className="form-check flex-grow-1 overflow-hidden align-items-center" htmlFor={`tri-network-${val.language}`}>
                {/* {network.id == 29 ?
                  <FontAwesomeIcon type={'solid'} icon={'star'} iconClass={'text-warning fs-6'} />
                  :
                  <Image height={20} width={20} className='me-2' src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network.id)}.svg`)} alt={GET_NETWORK_NAME(network.id)} fluid />
                } */}
                <span className="form-check-label text-gray-800 text-ellipsis fw-medium">{val.language}</span>
              </label>
              <input className="form-check-input flex-shrink-0" type="checkbox" id={`tri-network-${val.language}`} checked={selectLanguage.includes(val.language)} onChange={event => onSelectLanguage(val.language)} />
            </div>) :
              <div className="py-4">
                <h6 className="text-center fs-9 mb-0 text-gray-700">{formatTemplateText(pageContent?.opt_no_results, { section: pageContent?.language_title ?? "" })}</h6>
              </div>}
          </Dropdown.Menu>
        </Dropdown>
      </div>
      <div className='action_gap'></div>
    </>
  }
}


export default TLanguage